// Ï§ëÎ≥µÏù¥ ÎêòÎäî ÏöîÏÜåÎäî Ïú†ÏßÄÎ≥¥ÏàòÏóê Ïú†Ïö©ÌïòÏßÄ Î™ªÌïòÎã§, Î¨∂Ïñ¥Ï£ºÏûê
const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
// Î≥ÄÍ≤ΩÏùò Ïó¨ÏßÄÍ∞Ä ÏûàÎäî Í≤ÉÏùÄ Î≥ÄÏàòÎ°ú ÎπºÎëêÎäî Í≤ÉÏù¥ Ï¢ãÎã§ (data)
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';

const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

function getData(url) {
  ajax.open('GET', url, false);
  // data Í∞ÄÏ†∏Ïò¥
  ajax.send();

  return JSON.parse(ajax.response);
}

// data Ï≤òÎ¶¨(response to Object)
const newsFeed = getData(NEWS_URL);

const ul = document.createElement('ul');

window.addEventListener('hashchange', function () {
  const id = location.hash.substr(1);

  const newsContent = getData(CONTENT_URL.replace('@id', id));
  const title = document.createElement('h1');

  title.innerHTML = newsContent.title;
  content.appendChild(title);
  // console.log(newsContent);
});

for (let i = 0; i < 10; i++) {
  const div = document.createElement('div');
  const li = document.createElement('li');
  const a = document.createElement('a');

  // ÏïÑÏù¥Îü¨ÎãàÌïòÍ≤åÎèÑ DOM ÏÇ¨Ïö©Ïóê ÎåÄÌïú ÏßÅÍ¥ÄÏÑ± Í≤∞Ïó¨Ïùò Ìï¥Í≤∞ÏùÄ DOM ÏÇ¨Ïö©ÏùÑ ÏûêÏ†úÌïòÎäîÍ≤É. (Î¨∏ÏûêÏó¥ÏùÑ Ïù¥Ïö©ÌïòÏûê!)
  // ÏÑ§Î†π ÏñëÏù¥ Ï¢Ä ÎäòÏñ¥ÎÇòÍ≤å ÎêòÎçîÎùºÎèÑ Í∞ÄÎèÖÏÑ±Ïù¥ Ï¢ãÏùÄ Í≤ÉÏù¥ Ï¢ãÏùÄ ÎìØ ÌïòÎã§.
  div.innerHTML =
    `
  <li>
    <a href="#${newsFeed[i].id}">
      ${newsFeed[i].title} (${newsFeed[i].comments_count})
    </a>
  </li>
  `

  ul.appendChild(div.children[0]);
  // or div.firstElementChild
}

container.appendChild(ul);
container.appendChild(content);




